import React, { useEffect, useState } from 'react';
import { useHistory, useParams } from 'react-router-dom';
import { Nav} from 'react-bootstrap';
import styled from 'styled-components';
import './Detail.scss';

import {CSSTransition} from "react-transition-group"


let 박스 = styled.div`
padding: 20px;

`;
let 제목 = styled.h4`
font-size: 25px;
`;



function Detail(props) {

  let { id } = useParams();
  let history = useHistory();
  let 매칭 = props.data.find(function (상품) {
    return 상품.id == id
  });
  let [alert, alert변경] = useState(true);

  let [누른탭,누른탭변경] = useState(0);

  useEffect(() => {
    let 타이머 = setTimeout(function () {
      alert변경(false);
    }, 2000)
    return () => {
      clearTimeout(타이머);
    }
  }, []);



  return (
    <div className="container">


      <박스>
        <제목 className='red'>상세페이지</제목>
      </박스>
      {
        
        alert == true
          ? <div className='my-alert'>
            <p>재고가 얼마남지않았습니다.</p>
          </div>
          : null
      }
      <div className="row">
        <div className="col-md-6">
          <img src={"https://codingapple1.github.io/shop/shoes"+매칭.id+".jpg"} width="100%" />
        </div>
        <div className="col-md-6 mt-4">

          <h4 className="pt-5">{매칭.title}</h4>
          <p>{매칭.content}</p>
          <p>{매칭.price}</p>

          <Info 재고={props.재고} 매칭 = {매칭} />

          <button className="btn btn-danger" onClick={(i) => {
          

            let 재고copy = [...props.재고];
            재고copy[매칭.id]--;
            props.재고변경(재고copy)

          }}>주문하기</button>
          <button className="btn btn-danger" onClick={() => {
            history.goBack();
          }}>뒤로가기</button>

        </div>
      </div>


      <Nav className='mt-5' defaultActiveKey="/link-0">
        <Nav.Item>
          <Nav.Link eventKey="link-0" onClick={()=>{누른탭변경(0)}}>Active</Nav.Link>
        </Nav.Item>
        <Nav.Item >
          <Nav.Link eventKey="link-1" onClick={()=>{누른탭변경(1)}}>Link</Nav.Link>
        </Nav.Item>
      </Nav>
      <CSSTransition in={true} classNames="wow" timeout={500}>
     <TapContent 누른탭 = {누른탭}/>
     </CSSTransition>

    </div>
  )
}

function TapContent(props){
  if(props.누른탭 == 0){
    return  <div>0번째 내용</div>
  } else if(props.누른탭 == 1){
    return  <div>1번째 내용</div>
  } else if(props.누른탭 == 2){
    return  <div>2번째 내용</div>
  }
}


function Info(props) {
 

  return (
    <p>재고 : {props.재고[props.매칭.id]} </p>
  )
}

export default Detail;