import React, { useContext, useState } from 'react';
import { Navbar, Nav, Container } from 'react-bootstrap';
import './App.css';
import Data from './data';
import Detail from './Detail';
import { Link, Route, Switch } from 'react-router-dom';
import axios from 'axios';

let 재고context = React.createContext();

function App() {

  let [data, data변경] = useState(Data);
  let [재고, 재고변경] = useState([10,15,19]);



  return (
    <div className="App">

      <Navbar bg="dark" variant="dark">
        <Container>
          <Navbar.Brand href="#home">Navbar</Navbar.Brand>
          <Nav className="me-auto">
            <Nav.Link as={Link} to="/"> Home</Nav.Link>
            <Nav.Link as={Link} to="/detail/0">detail</Nav.Link>
          </Nav>
        </Container>
      </Navbar>

      <Switch>
        <Route exact path="/">

          <div className='main'>
            <img style={{ width: '100%' }} src="img/background.jpg" alt="이미지" />
          </div>

          <div className="container">
            
            <재고context.Provider value={재고}>
              
            <div className="row">
              {
                data.map((a, i) => {
                  return (
                    
                    <Card data={data[i]} i={i} key={i} />
                   
                  )
                })
              }
            </div>

            </재고context.Provider>

            <button onClick={()=>{
             
            axios.get('https://codingapple1.github.io/shop/data2.json')
            .then((결과)=>{console.log('성공')

            data변경([...data,...결과.data])

          })
            .catch(()=>{console.log('실패')})
            }} className='btn btn-primary'>더보기</button>
          </div>
        </Route>

        <Route path="/detail/:id">
          <Detail data={data} 재고 ={재고} 재고변경 ={재고변경} />
        </Route>

      </Switch>



    </div>
  );
}


function Card(props) {

  let 재고 = useContext(재고context);

  return (
    
    <div className="col-md-4">
      <Nav.Link as={Link} to={"/detail/"+(props.i+1) }> 
       <img src={"https://codingapple1.github.io/shop/shoes" + (props.i+1) + ".jpg"} width="100%" />
      <h4> {props.data.title}</h4>
      <p> {props.data.content}</p>
      <p> {props.data.price}</p>
      {재고[props.i]}
    </Nav.Link>
    </div>
  
  )
}

export default App;
